<style>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .ll-img-w {
    width: 60px;
    height: 60px;
  }
  .ll-img-w>img {
    width: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
  .del-store-icon-w {
    position: absolute;
    top: -8px;
    right: 2px;
    cursor: pointer;
  }

  .store-pre-time-item-wrap {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  }
  .store-pre-time-item-wrap>.item-left {
    flex: 0 0 90px;
    width: 90px;
    text-align: left;
    margin-left: 20px
  }
  .store-pre-time-item-wrap>.item-right {
    border: 1px solid #ececec;
    border-radius: 8px;
    padding: 8px;
    width: 550px;
    box-sizing: border-box;
  }
</style>
<template>
  <div>
    <div class="add-form-wraper">
      <el-form ref="form" :model="addParamMesg" label-width="140px">
        <el-form-item label="活动类型">
          <p style="color: #222;font-size: 16px; font-weight: bold;">{{addParamMesg.activityTypeName}}</p>
        </el-form-item>
        <el-form-item label="选择区域">
          <p>{{addParamMesg.regionName}}</p>
        </el-form-item>
        <el-form-item label="活动编码">
          <p>{{addParamMesg.activityCode}}</p>
        </el-form-item>
        <el-form-item label="活动名称">
          <p>{{addParamMesg.activityName}}</p>
        </el-form-item>
        <el-form-item label="开始时间">
          <p>{{addParamMesg.activityStartDate}}</p>
        </el-form-item>
        <el-form-item label="结束时间">
          <p>{{addParamMesg.activityEndDate}}</p>
        </el-form-item>
        <el-form-item label="门店/履约时间" required>
          <div class="store-pre-time-item-wrap" :class="index > 0 ? 'mt10' : null" v-for="(item, index) of activityStoreGroupList" :key="index">
            <div class="item-right">
              <div>
                <span>参与门店</span>
                <div v-if="item.activityStoresCheckedList.length > 0">
                  <el-table
                    class="fixed-height"
                    :data="item.activityStoresCheckedList"
                    empty-text="暂无选中任何门店"
                    max-height="250"
                    size="small">
                    <el-table-column min-width="226px" label="门店编码/名称">
                      <template slot-scope="scope">
                        <div>
                          {{scope.row.erpStoreNo}}_{{scope.row.erpStoreName}}
                        </div>
                      </template>
                    </el-table-column>
                    <el-table-column min-width="100" prop="saleTypeName" label="门店类型"></el-table-column>
                  </el-table>
                </div>
              </div>
              <div class="mt10">
                <span>履约时间</span>
                <span>{{item.performanceDate || '--'}}</span>
              </div>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="参与商品">
          <el-tabs v-model="activeName" @tab-click="handleTabClick">
            <el-tab-pane :name="item.activeName" v-for="(item, idx) in activityScenes" :key="idx">
              <span slot="label">
                {{item.label}} 
                <span v-if="item.activityStartTime && item.activityEndTime">
                  {{item.activityStartTime}}-{{item.activityEndTime}}
                </span>
              </span>
            </el-tab-pane>
            <join-goods-table ref="joinGoodsTable"></join-goods-table>
          </el-tabs>
        </el-form-item>

        <hr class="s-slip-tb">
        <el-form-item>
          <el-button size="small" @click="doCancelHandel">返回</el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>
<script>
  import * as utils from '@/utils/utils'
  import JoinGoodsTable from "./JoinGoodsTableShow"
  const reqUrl = {
    // 活动规则编辑跳转
    edit: "/plateform/a/activityRule/edit"
  }
  export default {
    data() {
      return {
        isDisabled: false,
        addParamMesg: {
          id: '',
          activityId: '', // 活动id
          regionCode: '',
          regionName: '',
          activityType: '', // 活动类型
          activityTypeName: '', // 活动类型名称
          activityName: '', // 活动名称
          activityStartDate: '', // 活动开始时间
          activityEndDate: '', // 活动结束时间
          activityCode: '', // 活动编码
          // 参与门店 秒杀中此字段没用，但是后端有非空验证，所以希望前端加上，故此处把所有门店分组中的门店编号传入
          activityStoreList: [],
          // 活动门店分组集合
          activityStoreGroupList: [],
          // 活动场次集合
          activityScenes: []
        },
        // 默认选中的场次
        activeName: 'first',
        scene: 1,
        // secnce 1 第一场 2 第二场 3 第三场 4 第四场 
        activityScenes: [
          {
            scene: 1, 
            activeName: 'first',
            label: '第一场',
            // 开始时间 格式：HH:mm:ss
            activityStartTime: '',
            // 结束时间 格式：HH:mm:ss
            activityEndTime: '',
            // 活动商品集合
            platCityGoodsInfos: [],
            // 当前选中的商品用于批量设置价格
            checkedList: [],
            // 批量修改折扣
            discount: ''
          },
          {
            scene: 2,
            activeName: 'second', 
            label: '第二场',
            // 开始时间 格式：HH:mm:ss
            activityStartTime: '',
            // 结束时间 格式：HH:mm:ss
            activityEndTime: '',
            // 活动商品集合
            platCityGoodsInfos: [],
            // 当前选中的商品用于批量设置价格
            checkedList: [],
            // 批量修改折扣
            discount: ''
          },
          {
            scene: 3, 
            activeName: 'third', 
            label: '第三场',
            // 开始时间 格式：HH:mm:ss
            activityStartTime: '',
            // 结束时间 格式：HH:mm:ss
            activityEndTime: '',
            // 活动商品集合
            platCityGoodsInfos: [],
            // 当前选中的商品用于批量设置价格
            checkedList: [],
            // 批量修改折扣
            discount: ''
          },
          {
            scene: 4, 
            activeName: 'fourth', 
            label: '第四场',
            // 开始时间 格式：HH:mm:ss
            activityStartTime: '',
            // 结束时间 格式：HH:mm:ss
            activityEndTime: '',
            // 活动商品集合
            platCityGoodsInfos: [],
            // 当前选中的商品用于批量设置价格
            checkedList: [],
            // 批量修改折扣
            discount: ''
          }
        ],
        // 参与门店与履约时间
        activityStoreGroupList: [
          {
            performanceDate: '', // 履约时间
            activityStoresCheckedList: [] // 选中的门店
          }
        ]

      }
    },
    created() {
      let activityId = this.$route.params.activityId
      this.addParamMesg.activityId = activityId
    },
    mounted() {
      this.getDetail()
    },
    components: {
      "join-goods-table": JoinGoodsTable
    },
    methods: {
      /**
       * [getDetail 获取活动详情]
       * @return {[type]} [description]
       */
      getDetail() {
        let activityId = this.addParamMesg.activityId || ''
        utils.showLoading()
        this.$http.post(reqUrl.edit, {activityId: activityId}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              let createGoodsMesg = res.data
              this.addParamMesg.id = createGoodsMesg.id
              this.addParamMesg.activityId = createGoodsMesg.id
              this.addParamMesg.regionCode = createGoodsMesg.regionCode || ''
              this.addParamMesg.regionName = createGoodsMesg.regionName || ''
              this.addParamMesg.activityType = createGoodsMesg.activityType || ''
              this.addParamMesg.activityTypeName = createGoodsMesg.activityTypeName || ''
              this.addParamMesg.activityName = createGoodsMesg.activityName || ''
              this.addParamMesg.activityStartDate = createGoodsMesg.activityStartDate || ''
              this.addParamMesg.activityEndDate = createGoodsMesg.activityEndDate || ''
              this.addParamMesg.activityCode = createGoodsMesg.activityCode || ''
              if(createGoodsMesg.storeVos && createGoodsMesg.storeVos.length) {
                this.activityStoresCheckedList = createGoodsMesg.storeVos
              }
              // 处理选中的门店
              this.activityStoreGroupList = createGoodsMesg.storeGroupVos && createGoodsMesg.storeGroupVos.length ? createGoodsMesg.storeGroupVos.map(item => {
                return {
                  performanceDate: item.performanceDatetime ? item.performanceDatetime.substr(0, 10) : '',
                  activityStoresCheckedList: item.storeVos
                }
              }) : [{
                performanceDate: '',
                activityStoresCheckedList: []
              }]
              // 处理参与商品数据
              if(createGoodsMesg.activityScenes && createGoodsMesg.activityScenes.length) {
                let activityScenesTemp = createGoodsMesg.activityScenes
                for(let i of this.activityScenes) {
                  for(let j of activityScenesTemp) {
                    if(i.scene === j.scene) {
                      i.activityStartTime = j.activityStartTime.slice(0, 5)
                      i.activityEndTime = j.activityEndTime.slice(0, 5)
                      i.platCityGoodsInfos = j.platCityGoodsVos
                    }
                  }
                }
              }

              this.setJoinGoodsMesg(this.scene)
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },

       /**
       * [handleTabClick tab点击切换]
       */
      handleTabClick(_val) {
        if(_val.name == 'first') {
          this.scene = 1
        }else if(_val.name == 'second') {
          this.scene = 2
        }else if(_val.name == 'third') {
          this.scene = 3
        }else if(_val.name == 'fourth') {
          this.scene = 4
        }

        this.activeName = _val.name
        this.setJoinGoodsMesg(this.scene)
      },
      /**
       * [setJoinGoodsMesg 设置表格组件数据]
       * @param {[type]} _scene [description]
       */
      setJoinGoodsMesg(_scene) {
        let tableData = []
        let checkedList = []

        for(let item of this.activityScenes) {
          if(item.scene === _scene) {
            tableData = item.platCityGoodsInfos
            checkedList = item.checkedList
          }
        }

        let _value = {
          scene: _scene,
          checkedList: checkedList,
          tableData: tableData
        }
        this.$refs.joinGoodsTable.setDefaultMesg(_value)
      },
      /**
       * [doCancelHandel 返回]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$router.push('/store_act')
      }
    },

    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }

</script>



