<template>
  <div>
    <div v-show="tableData.length > 0">
      <el-table
        class="fixed-height"
        :data="tableData"
        ref="multipleTable"
        empty-text="暂无选中任何商品"
        style="width:100%;"
        max-height="400"
        size="small">
        <el-table-column min-width="220px" label="商品信息">
          <template slot-scope="scope">
            <div class="ll-p-m-w">
              <div class="ll-img-w">
                <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                <img v-else src="/shop/static/img_error.png" alt="">
              </div>
              <div class="ll-p-m-c-r">
                <div>
                  {{scope.row.goodsName || '-'}}
                </div>
                <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                <div class="ll-erp-p-no">商品ID：{{scope.row.id}}</div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
        <el-table-column min-width="130px" label="活动商品名称">
          <template slot-scope="scope">
            <div>
              {{scope.row.activityGoodsName || '-'}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="90px" prop="price" label="原价"></el-table-column>
        <el-table-column min-width="110px" label="活动价">
          <template slot-scope="scope">
            <div>
              {{scope.row.groupPrice}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="150px" label="限购数量(为空不限)">
          <template slot-scope="scope">
            <div>
              {{scope.row.limitGoodsCount}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="110px" label="活动库存">
          <template slot-scope="scope">
            <div>
              {{scope.row.curStock}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="110px" label="排序权重">
          <template slot-scope="scope">
            <div>
              {{scope.row.sortNum}}
            </div>
          </template>
        </el-table-column>
        <el-table-column min-width="170px" :render-header="sfInStockHeader">
          <template slot-scope="scope">
            <div>{{scope.row.sfInStock ? '是' : '否'}}</div>
          </template>
        </el-table-column>
      </el-table>
      <div>
        <span style="color: red;">*</span>注意：权重排序，先创建的活动商品在前；剩余的按前端规则走。
      </div>
    </div>
  </div>
</template>

<script>
  import * as utils from '@/utils/utils'
  export default {
    data() {
      return {
        tableData: [],
        scene: '',
        checkedList: []
      }
    },

    methods: {
      /**
       * [setDefaultMesg 设置信息]
       * @param {[type]} _val [description]
       */
      setDefaultMesg(_val) {
        let tableDataTemp = _val.tableData || []
        for(let item of tableDataTemp) {
          item.copyUrl = `pages/product/detail?id=${item.platformCityGoodsId}&type=4&activityPlatCityGoodsId=${item.id}`
        }
        this.tableData = tableDataTemp
        this.checkedList = _val.checkedList || []
        this.scene = _val.scene || ''
      },
      sfInStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>1、顺丰标品设为顺丰仓现货时，下单会校验仓库库存；</p>
              <p>2、本来鲜生鲜品无需设置；</p>
            </div>
            <span slot="reference">是否为顺丰仓现货<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * [copyUrl 复制链接成功]
       * @return {[type]}      [description]
       */
      onCopySuc() {
        this.$message.success("链接已复制至剪切板，仅限微信公众号中使用")
      },
      /**
       * [onCopyError 复制链接失败]
       * @return {[type]} [description]
       */
      onCopyError() {
        this.$message.error("复制链接失败,请手动复制")
      }

    }
  }
</script>


